import { toRefs, reactive } from 'vue';
export default {
  name: 'searchnav',
  setup() {
    const data = reactive({
      jisunum: false,
      navli: [{
        id: 'n001',
        name: '全部'
      }, {
        id: 'n002',
        name: '招呼'
      }, {
        id: 'n003',
        name: '沟通中'
      }],
      num: 0
    });
    const changenavli = index => {
      data.num = index;
    };
    return {
      ...toRefs(data),
      changenavli
    };
  }
};